<template>
	<view class="mine-container" :style="{height: `${windowHeight}px`}">
		<view class="content">
			<!-- 搜索框 -->
			<view class="search-bar">
				<input type="text" placeholder="请输入搜索关键词" @input="onSearchInput" />
			</view>
 



			<!-- 列表 -->
			<scroll-view  scroll-y class="list" @scrolltolower="onScrollToLower" refresher-enabled
				:refresher-triggered="triggered" @refresherpulling="onPulling" @refresherrefresh="onRefresh">
				<view class="list-item" v-for="(item, index) in listItems" :key="index" @click="onListItemClick(item)">
					<view class="point-text">
						<view class="title">{{ item.title }}</view>
					</view>
					 <view class="tags">
					        <view class="tag" v-for="(tag, tagIndex) in item.tags" :key="tagIndex" :style="{ backgroundColor: tag.color }">
					          {{ tag.text }}
					        </view>
					     </view>
					<view style="color: #8f8f94;display:flex;justify-content: space-between;" class="point-text">
						<text>{{ item.create_time }}</text>
						<text>{{ item.resource }}</text>
					</view>
				</view>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	import storage from '@/utils/storage'
	import constant from '@/utils/constant'
	import {
		getUserReportList
	} from '@/api/report'
	export default {
		data() {
			return {
				triggered: false,
				searchValue: '',
				selectedIndex: 0, // 新增，用于跟踪当前选中的标签索引
				tabItems: [{
						name: '热门推荐'
					},
					{
						name: '热门行业'
					},
					{
						name: '查看全部'
					},
				],
				total: '',
				hasMore: true,
				downForm: {
					userId: storage.get(constant.userId),
					keyWord: '',
					pageSize: 10,
					pageIndex: 1,
					start: '',
					end: '',
					type: ''
				},
				listItems: [
					// 这里添加列表项的数据
				],
			};
		},
		mounted() {
		 
		},
		onLoad(options) {
		this.downForm.type = options.type; // 输出：1
		this.getUserReportList();
		},
		created() {
		},
		methods: {
			getUserReportList() {
				getUserReportList(this.downForm).then(res => {
					if (res.data && res.data.list) {
						this.listItems = this.listItems.concat(res.data.list);
						this.listItems.forEach(item => {
						     item.tags = item.tag.split(',').map(tagText => ({
						            text: tagText,
						            color: this.getRandomColor(),
						          }));
						   });
						this.total = res.data.total;
					}
				})
			},
			onPulling() {
				var that = this;
				if (!this.triggered) {
					//下拉加载，先让其变true再变false才能关闭
					this.triggered = true;
					//关闭加载状态 (转动的圈)，需要一点延时才能关闭
					setTimeout(() => {
						that.triggered = false;
					}, 1000)
				}
			},
			onRefresh() {
				console.log("下拉刷新");
				this.hasMore = true;
				this.downForm.pageIndex = 1;
				
			},
			onScrollToLower() {
            console.log("1111")
				if (this.hasMore) {
					this.loadMore();
				}
			},
			loadMore() {
				if (this.downForm.pageIndex * this.downForm.pageSize >= this.total) {
					this.hasMore = false;
					return;
				}
				this.downForm.pageIndex++;
				//this.getUserPointsRecord();
			},
			onSearchInput(e) {
				this.searchValue = e.detail.value;
				this.downForm.keyWord = e.detail.value;
				getUserReportList(this.downForm).then(res => {
					if (res.data && res.data.list) {
						this.listItems = res.data.list;
						this.total = res.data.total;
					}
				})
			},
			onTabItemClick(index) {
				this.selectedIndex = index; // 更新选中的标签索引
				console.log('点击了', this.tabItems[index].name);
			},
			onListItemClick(item) {
				console.log('点击了列表项', item);
			},
			getRandomColor() {
			       // 预定义的颜色集合
			          const colors = ['#FAF0E6', '#87CEEB', '#66CDAA', '#CD5C5C', '#DDA0DD','#C1CDC1','#D1EEEE','#EED2EE','#9F79EE'];
			          // 从集合中随机选择一个颜色
			          return colors[Math.floor(Math.random() * colors.length)];
			    },
		},
	};
</script>

<style>
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.search-bar {
		padding: 10rpx;
		width: 95%;
		margin-top: 15rpx;
		background-color: white;
		border-radius: 10rpx;
	}


	

	.list {
		height: 94vh;
		/* 根据实际情况调整高度 */
		overflow-y: auto;
		/* 确保内容超出时可以滚动 */
		width: 99%;
		padding: 10rpx;

		border-radius: 5rpx;
	}

	.list-item {
		display: flex;
		flex-direction: column;
		margin-bottom: 20rpx;
		background-color: white;
		border-radius: 10rpx;
	}
	.point-text {
		padding: 30rpx;
	}
	.tags {
	  display: flex;
	  flex-wrap: wrap;
	  margin-left: 20rpx;
	}
	.tag {
	  padding: 4px 8px;
	  margin: 4px;
	  border-radius: 4px;
	  color: white;
	  font-size: 8px;
	}
</style>